.footer {
	padding: 70px 0px 117px;
	text-align: center;
	color: #fff;

	.footer__list-title {
		font-weight: 700;
		font-size: 30px;
		line-height: 40px; }

	.footer__list-descr {
		font-size: 20px;
		font-weight: 400;
		margin-top: -7px;
		line-height: 25px; } }

.logo-white_footer {
	max-width: 300px;
	height: 63px;
	margin: 37px auto; }

.social {
	text-align: center;
	margin-top: 64px;

	.social-icon {
		display: inline-block;
		height: 80px;
		width: 80px;
		border-radius: 50%;
		position: relative;
		&:nth-child(2) {
			margin: 0px 30px;
			@include r(480) {
				margin: 0px 10px; } }
		a {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 50%; } }

	$social: facebook #4867aa 45px #f6ab49, linkedin #3092d1 35px #f6f219, vk #4c75a3 45px #f012e9;

	.social-icon {
		svg {
			fill: #fff;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%); }
		@each $name, $color, $fontSize, $hoverBg in $social {
			&_#{$name} {
				background-color: $color;
				svg {
					font-size: $fontSize; }
				:hover {
					background-color: $hoverBg; } } } } }
